.lrn-qe.lrn-qe-modal[data-algorithmic-modal] {
    .lrn-qe-modal-dialog {
        max-width: 60em;
    }

    .lrn-qe-modal-content {
        min-height: 100px;
        position: relative;
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .lrn-qe-modal-footer ul {
        float: right;
        width: 40%;
    }
}

[data-modal-notification] {
    float: left;
    max-width: 60%;
    padding: 15px;
    margin-bottom: 10px;
    background-color: #d9edf7;
    color: #31708f;
    border: 1px solid #bce8f1;
    border-radius: 4px;
}



