.lrn-qe .lrn-qe-ui {
    .lrn-qe-btn{
        &[disabled] {
            opacity: .5;

            &:hover,
            &:focus {
                background: #eaeaea;
                cursor: not-allowed;
            }
        }
    }

    .lrn-qe-btn-add-variables-wrapper .lrn-qe-btn,
    .lrn-qe-btn-add-response {
        padding: .8em 1em;
        font-size: 1em;
        text-transform: capitalize;
    }


    .lrn-qe-btn-add-response {
        color: #fff;
        background-color: #f0ad4e;
        border-color: #eea236;

        &[disabled] {
            &:hover,
            &:focus {
                background-color: #f0ad4e;
            }
        }
    }

    .lrn-qe-toolbar {
        margin-left: -20px;
        margin-right: -20px;
        padding-left: 20px;
        padding-right: 20px;
    }

    .lrn-qe-template-markup-controls {
        margin: 15px 0 5px 0;
        min-height: 40px;

        > [class^="lrn-qe-col-"],
        > [class*=" lrn-qe-col-"] {
            padding-left: 0;
        }
    }

    .lrn.lrn_widget .lrn_bordered_mathinput {
        border-color: #939393;
        border-radius: 0;

        &.lrn_focused {
            border-color: #1877b1;
        }
    }

    .lrn-qe-fieldset {
        border: 1px solid #ececec;

        .lrn-qe-legend {
            display: block;
            padding: 10px;
            background: #ececec;
            margin: -0.35em -0.625em 0 -0.625em;
        }
    }

    .lrn-qe-datatable-controls {
        text-align: right;

        .lrn-qe-btn-edit-datatable {
            border: 1px solid #d9d9d9;

            &:hover,
            &:focus {
                background: #eaeaea;
            }
        }
    }

    .lrn-qe-dyn-response-var {
        .lrn-qe-i-dynamic-data:before {
            content: "\e913";
        }
    }

    [data-lrn-mathcore-failed-notification] {
        padding: 15px;
        color: #8a6d3b;
        background-color: #fcf8e3;
        border: 1px solid #faebcc;
        border-radius: 2px;
    }

    .lrn-qe-inline > * {
        display: inline;
    }

    .lrn-qe-note-text {
        font-size: .8em;
        color: #666;
    }
}



