.lrn-qe-spinner {
    @include centerer();
    text-align: center;

    .lrn-qe-bounce1 {
        animation-delay: -.32s;
    }

    .lrn-qe-bounce2 {
        animation-delay: -.16s;
    }

    > div {
        width: 9px;
        height: 9px;
        margin-right: 1px;
        background-color: #ccc;
        border-radius: 100%;
        display: inline-block;
        animation: bouncedelay 1.4s both infinite ease-in-out;
    }
}

.lrn-qe-reloading {
    display: inline-block;
    margin-right: 10px;
    opacity: 0;
    color: #666;

    .lrn-qe-loader {
        padding: 3px 0 3px 30px;
        background: url('../images/loader.gif') 0 -2px no-repeat;
    }
}



