$width: 800px;

@mixin centerer($horizontal: true, $vertical: true) {
    position: absolute;

    @if ($horizontal and $vertical) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

    } @else if ($horizontal) {
        left: 50%;
        transform: translate(-50%, 0);

    } @else if ($vertical) {
        top: 50%;
        transform: translate(0, -50%);
    }
}

body {
    padding: 0;
    margin: 0;
    background: #f3f5f5;
}

.container {
    position: relative;

    .header {
        background: #5413AA;
        min-height: 200px;

        .header-content {
            max-width: $width;
            margin: 0 auto;
            padding-top: 50px;
            text-align: center;
            color: #fff;
            font-size: 30px;
            font-weight: 100;
            font-family: 'Helvetica Neue',Helvetica,Arial,sans-serif;

            .logo {
                padding: 3px 0 3px 60px;
                background: url('https://demos.learnosity.com/static/images/logo-square.svg') center left no-repeat;
                background-size: 40px;
            }
        }
    }

    .shadow-page {
        max-width: $width;
        min-height: 300px;
        margin: 40px auto;
        padding: 20px;
        background: #fff;
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    }

    .my-editor {
        position: relative;
        margin-top: -81px;

        @import "apis/questionEditor";
        @import "components/loader";
    }
}

.hidden {
    display: none !important;
}

@import "components/backgrid";
@import "components/modal";
@import "components/helpSection";



